html,
body {
  margin: 0;
  padding: 0;
}

.App {
  display: flex;

  height: 100vh;
  width: 100vw;
}

/* FROM ATHENA */

.container-thumb {
  position: relative;
  /* width: 600px; */
  height: 95vh;
}

.image-thumb {
  display: block;
  width: 100%;
  height: auto;
}

.overlay-thumb {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  height: 100%;
  width: 100%;
  opacity: 0.25;
  transition: 0.2s ease;
}

.container-thumb:hover .overlay-thumb {
  opacity: 0.8;
}

.top-left {
  position: absolute;
  top: 0px;
  left: 0px;
  width: 100%;
  height: 100%;
}
